* {
    box-sizing: border-box;
    font-family: 'Lato', sans-serif;
  }

  body,
  html,
  #root {
    background-color: #e3e8ed;
    display: flex;
    flex-direction: column;
    flex: 1;
    margin: 0;
    padding: 0;
    color: #000;
    font-size: 0.9em
  }
.visibility-hidden {
  visibility: hidden;
}
/*
Login Container
*/
#login-container {
  margin: 0 auto;
  width: 500px;
  transform: translateY(60%);
  padding: 50px 40px;
  border-radius: 2px;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.3);
}
#login-container .row {
  margin-top: 25px;
}
#login-container .alert {
  margin-top: 10px;
  padding: 5px;
}
#login-container #login-btn .btn{
  width: 150px;
  height: 40px;
}
/*
Login container ends
*/

#search-container {
  width: 100%;
  padding: 5px 30px;
  border-radius: 2px;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.3);
}
#suggestion-container {
  width: 100%;
  margin-top: 1.3em;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.3);
}
#suggestion-container li:hover {
  color: rgb(63, 63, 156);
  cursor: pointer;
}

#no-match {
  width: 100%;
  margin-top: 1.5em;
  text-align: center
}
#no-match i {
  font-size: 110px;
}

#no-match h5 {
  line-height: 60px;
}
ul {
  list-style-type: none;
}
p span {
  font-weight: 400
}
.emailAddress {
  font-size: 0.7em;
  padding: 3px;
}

.fas {
  cursor: pointer;
  font-size: 1.2em;
}
.api-modal {
  color: #4e6e05;
  position: absolute;
  top: 0px;
  right: 20px;
  width: 300px;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.3);
  background-color: #c2c8cdeb;
}
.api-modal p { text-align: center;}
.api-modal input {
  margin: .6rem;
}
